import React, { useContext } from 'react';
import { LeagueContext, LeagueConsumer } from './main.js';
import styled from 'styled-components';

const LeagueFrameTotal = (props) => {
	const context = useContext(LeagueContext);
	const remaining = context.noOfFrames - (context.keyboard.targetFrame);

	console.log('REMAINING', remaining)
	console.log('CURRENT TOTAL', context.currentTotal)

	return <LeagueConsumer>
		{(context) => (
			<FrameWrapper>
				<Frame>{context.currentTotal}</Frame>
				<Frame>{context.currentTotal + (remaining * 50)}</Frame>
				<Frame>{context.noOfFrames * 50}</Frame>
			</FrameWrapper>
		)}
	</LeagueConsumer>
};

const FrameWrapper = styled.div`
	width: 80px;
	padding: 4px 2px;
	margin-left: 16px;
`

const Frame = styled.div`
	border-radius: 2px;
	height: 50px;
	width: 50px;
	display: flex;
	flex-direction: column;
	margin-bottom: 4px;
	opacity: initial;
	background: rgb(255, 255, 255);
	box-shadow: 0 0 1px 1px rgba(0,0,0,.05), 0 1px 2px -2px rgba(0,0,0,.06), 0 1px 3px 0 rgba(0,0,0,.07);
	text-align: center;
	padding-top: 16px;
	box-sizing: border-box;
	transition: box-shadow .1s linear;

	:hover {
		box-shadow: rgba(0, 0, 0, 0.16) 0px 0px 1px 0px, rgba(0, 0, 0, 0.05) 0px 2px 2px 0px, rgba(0, 0, 0, 0.06) 0px 3px 1px -2px, rgba(0, 0, 0, 0.1) 0px 1px 5px 0px;
	}
`

export default LeagueFrameTotal;
