import React, { useContext } from 'react';
import { LeagueContext, LeagueConsumer } from './main.js';
import styled from 'styled-components';

const LeagueFrame = (props) => {
	const { frameNo } = props;
	const context = useContext(LeagueContext);
	const scores = context.scoreMatrix[frameNo];
	const isTarget = context.keyboard.targetFrame === frameNo;

	return <LeagueConsumer>
		{(context) => (
			<FrameWrapper>
				<Frame>{scores && scores.distance || 0}</Frame>
				{!isTarget &&
					<FrameEditable
						onClick={() => context.showKeyboard(frameNo)}>
						{scores && scores.score || 0}
					</FrameEditable>
				}
				{isTarget &&
					<FrameEditableTarget
						onChange={(event) => context.inputChange(frameNo, event)}
						onFocus={handleFocus}
						type='text'
						name='name'
						value={scores && scores.score || 0} />
				}
				<Frame>{scores && scores.total || 0}</Frame>
			</FrameWrapper>
		)}
	</LeagueConsumer>
};

const handleFocus = (ev) => {
	ev.target.select();
}

const FrameWrapper = styled.div`
	width: 80px;
	padding: 4px 2px;
`

const Frame = styled.div`
	border-radius: 2px;
	height: 50px;
	width: 50px;
	display: flex;
	flex-direction: column;
	margin-bottom: 4px;
	opacity: initial;
	background: rgb(255, 255, 255);
	box-shadow: 0 0 1px 1px rgba(0,0,0,.05), 0 1px 2px -2px rgba(0,0,0,.06), 0 1px 3px 0 rgba(0,0,0,.07);
	text-align: center;
	padding-top: 16px;
	box-sizing: border-box;
	transition: box-shadow .1s linear;

	:hover {
		box-shadow: rgba(0, 0, 0, 0.16) 0px 0px 1px 0px, rgba(0, 0, 0, 0.05) 0px 2px 2px 0px, rgba(0, 0, 0, 0.06) 0px 3px 1px -2px, rgba(0, 0, 0, 0.1) 0px 1px 5px 0px;
	}
`

const FrameEditable = styled(Frame)`
	cursor: pointer;
`

const FrameEditableTarget = styled.input`
	border-radius: 2px;
	height: 50px;
	width: 50px;
	display: flex;
	flex-direction: column;
	margin-bottom: 4px;
	opacity: initial;
	background: rgb(255, 255, 255);
	box-shadow: 0 0 1px 1px rgba(0,0,0,.05), 0 1px 2px -2px rgba(0,0,0,.06), 0 1px 3px 0 rgba(0,0,0,.07);
	text-align: center;
	box-sizing: border-box;
	transition: box-shadow .1s linear;	
	cursor: pointer;
	border: solid 2px #317ae2;
	padding-top: 0px;
	max-width: 80px;
	font-size: 16px;
`

export default LeagueFrame;
