import React from 'react';
import { LeagueConsumer } from './main.js';
import LeagueKey from './key.js'

const LeagueKeyboard = (props) => {
	return <LeagueConsumer>
		{(context) => (
			createKeys(props.range[1])
		)}
	</LeagueConsumer>
};

const createKeys = (count) => {
	let keys = [];

	for (let i = 0; i < count; i++) {
		keys.push(<LeagueKey key={i} keyNo={i} />)
	}

	return keys;
}

export default LeagueKeyboard;
