import React from 'react';
import { LeagueConsumer } from './index.js';
import styled from 'styled-components';

const LeagueKey = (props) => {
	const { keyName, keyValue, fontColor } = props;

	//undo button needed

	return <LeagueConsumer>
		{(context) => (
			<Key fontColor={fontColor} onClick={() => context.scoreChange(keyValue)}>{keyName}</Key>
		)}
	</LeagueConsumer>
};

const Key = styled.div`
	border-radius: 2px;
	height: 50px;
	width: 50px;
	display: flex;
	flex-direction: column;
	margin: 4px 4px;
	opacity: initial;
	background: rgb(255, 255, 255);
	box-shadow: 0 0 1px 1px rgba(0,0,0,.05), 0 1px 2px -2px rgba(0,0,0,.06), 0 1px 3px 0 rgba(0,0,0,.07);
	text-align: center;
	padding-top: 16px;
	box-sizing: border-box;
	transition: box-shadow .1s linear;
	cursor: pointer;
	color: ${props => props.fontColor || '#317ae2'};

	:hover {
		box-shadow: rgba(0, 0, 0, 0.16) 0px 0px 1px 0px, rgba(0, 0, 0, 0.05) 0px 2px 2px 0px, rgba(0, 0, 0, 0.06) 0px 3px 1px -2px, rgba(0, 0, 0, 0.1) 0px 1px 5px 0px;
	}
`

export default LeagueKey;
