import React, { useContext } from 'react';
import { LeagueContext, LeagueConsumer } from './index.js';
import styled from 'styled-components';

const LeagueFrame = (props) => {
	const { frameNo } = props;
	const context = useContext(LeagueContext);
	const scores = context.scoreMatrix[frameNo];
	const { targetFrame } = context;

	if (scores) {
		return <LeagueConsumer>
			{(context) => (
				<FrameWrapper>
					{renderFrames(scores, targetFrame, frameNo, context)}
				</FrameWrapper>
			)}
		</LeagueConsumer>
	}

	return <FrameWrapper></FrameWrapper>
};

const renderFrames = (scores, targetFrame, frameNo, context) => {
	let frames = [];

	Object.keys(scores).map(key => {
		if (targetFrame.frame === frameNo && targetFrame.distance === Number(key)) {
			frames.push(
				<FrameEditableTarget
					onChange={(event) => context.inputChange({frame: frameNo, distance: Number(key)}, event)}
					onFocus={handleFocus}
					type='text'
					name='name'
					key={key}
					value={scores[key]} />
			)
		} else {
			frames.push(<FrameEditable key={key} onClick={() => context.showKeyboard({frame: frameNo, distance: Number(key)})}>{scores[key]}</FrameEditable>)
		}
	})

	return frames;
}

const handleFocus = (ev) => {
	ev.target.select();
}

const FrameWrapper = styled.div`
	width: 80px;
	padding: 4px 2px;
`

const Frame = styled.div`
	border-radius: 2px;
	height: 50px;
	width: 50px;
	display: flex;
	flex-direction: column;
	margin-bottom: 4px;
	opacity: initial;
	background: rgb(255, 255, 255);
	box-shadow: 0 0 1px 1px rgba(0,0,0,.05), 0 1px 2px -2px rgba(0,0,0,.06), 0 1px 3px 0 rgba(0,0,0,.07);
	text-align: center;
	padding-top: 16px;
	box-sizing: border-box;
	transition: box-shadow .1s linear;
	overflow: scroll;

	:hover {
		box-shadow: rgba(0, 0, 0, 0.16) 0px 0px 1px 0px, rgba(0, 0, 0, 0.05) 0px 2px 2px 0px, rgba(0, 0, 0, 0.06) 0px 3px 1px -2px, rgba(0, 0, 0, 0.1) 0px 1px 5px 0px;
	}
`

const FrameEditable = styled(Frame)`
	cursor: pointer;
`

const FrameEditableTarget = styled.input`
	border-radius: 2px;
	height: 50px;
	width: 50px;
	display: flex;
	flex-direction: column;
	margin-bottom: 4px;
	opacity: initial;
	background: rgb(255, 255, 255);
	box-shadow: 0 0 1px 1px rgba(0,0,0,.05), 0 1px 2px -2px rgba(0,0,0,.06), 0 1px 3px 0 rgba(0,0,0,.07);
	text-align: center;
	box-sizing: border-box;
	transition: box-shadow .1s linear;	
	cursor: pointer;
	border: solid 2px #317ae2;
	padding-top: 0px;
	max-width: 80px;
	font-size: 16px;
`

export default LeagueFrame;



