import React, { Component, createContext } from 'react';
import LeagueFrameContainer from './frame-container.js';
import LeagueFrameTotal from './frame-total.js';
import LeagueDistances from './frame-distances.js';
import LeagueKeyboard from './keyboard.js';
import styled from 'styled-components';

const LeagueContext = createContext({});
const LeagueProvider = LeagueContext.Provider;
const LeagueConsumer = LeagueContext.Consumer;

// show only first frame
// after frame clear it and update total score
// shows previous round scores in gray boxes
// add round number at the top
// make keys bigger

export class Trend extends Component {
	constructor(props) {
		super(props);

		this.state = {
			noOfFrames: 20,
			totalThrows: 0,
			currentTotal: 0,
			frameTotal: 0,
			scoreMatrix: [],
			targetFrame: {
				frame: 0,
				distance: 5,
			},
			keyboard: {
				shouldShow: false,
			},
			showKeyboard: (target) => {
				this.setState({
					keyboard: {shouldShow: true},
					targetFrame: {frame: target.frame, distance: target.distance}
				});
			},
			increaseTotal: () => {
				this.setState({currentTotal: this.state.currentTotal + 1});
			},
			inputChange: (frame, ev) => this.handleInputChange(frame, ev),
			scoreChange: (keyValue) => this.handleScoreChange(keyValue)
		}
	}

	componentDidMount() {
		this.createScoreMatrix(this.state.noOfFrames);
	}

	createScoreMatrix(noOfFrames) {
		let matrix = [];

		for (let i = 0; i < noOfFrames; i++) {
			matrix.push({5: '', 6: '', 7: '', 8: '', 9: '', 10: ''})
		}

		this.setState({scoreMatrix: matrix});
	}

	calculateTotal() {
		const { scoreMatrix } = this.state;

		let totalScore = 0;

		scoreMatrix.forEach((scores) => {
			for (let [key, value] of Object.entries(scores)) {
				totalScore += (key * parseInt(value + 0, 10));
			}
		});

		return totalScore;
	}

	handleInputChange(frame, ev) {
	// 	const scoreMatrix = this.state.scoreMatrix;

	// 	scoreMatrix[frame].score = ev.currentTarget.value;
	// 	this.setState({scoreMatrix: scoreMatrix});
	}

	handleScoreChange(keyValue) {
		const { scoreMatrix, frameTotal, targetFrame, totalThrows } = this.state;
		const newFrameTotal = frameTotal < 5 ? frameTotal + 1 : 1;

		this.setState({
			scoreMatrix: this.setScoreMatrix(scoreMatrix, targetFrame, keyValue),
			frameTotal: newFrameTotal,
			totalThrows: totalThrows + 1,
			targetFrame: this.setTargetFrame(newFrameTotal, targetFrame, keyValue),
			currentTotal: this.calculateTotal()
		});
	}

	setScoreMatrix(scoreMatrix, targetFrame, keyValue) {
		if (keyValue === 1) {
			scoreMatrix[targetFrame.frame][targetFrame.distance] = parseInt(scoreMatrix[targetFrame.frame][targetFrame.distance] + 1, 10);
		}

		return scoreMatrix;
	}

	setTargetFrame(newFrameTotal, targetFrame, keyValue) {
		if (newFrameTotal < 5) {
			if (keyValue === 0 && targetFrame.distance > 5) {
				return {
					frame: targetFrame.frame,
					distance: targetFrame.distance - 1
				}
			} else if (keyValue === 0 && targetFrame.distance === 5) {
				return targetFrame;
			} else if (keyValue === 1 && targetFrame.distance < 10) {
				return {
					frame: targetFrame.frame,
					distance: targetFrame.distance + 1
				}
			}
			return targetFrame
		} else {
			if (keyValue === 0 && targetFrame.distance > 5) {
				return {
					frame: targetFrame.frame + 1,
					distance: targetFrame.distance - 1
				}
			} else if (keyValue === 0 && targetFrame.distance === 5) {
				return {
					frame: targetFrame.frame + 1,
					distance: targetFrame.distance
				}
			} else if (keyValue === 1 && targetFrame.distance < 10) {
				return {
					frame: targetFrame.frame + 1,
					distance: targetFrame.distance + 1
				}
			}
			return {
				frame: targetFrame.frame + 1,
				distance: targetFrame.distance
			}
		}
	}

	render () {
		return <LeagueContext.Provider value={this.state}>
			<LeagueMain>
				<LeagueDistances />
				<LeagueFrameContainer />
				<LeagueFrameTotal />
			</LeagueMain>
			<LeagueKeyboardContainer>
				<LeagueKeyboard range={[0, 2]} />
			</LeagueKeyboardContainer>
		</LeagueContext.Provider>
	}
}

const LeagueMain = styled.div`
	width: 100%;
	height: 100%;
	padding: 40px;
	box-sizing: border-box;
	display: flex;
	flex-direction: row;
	justify-content: center;
	background: #f7f7f7;
`;

const LeagueKeyboardContainer = styled.div`
	padding: 50px;
	width: 100%;
	background: #f7f7f7;
	display: flex;
	flex-direction: row;
	justify-content: center;
	box-sizing: border-box;
`;

export {
	LeagueContext,
	LeagueProvider,
	LeagueConsumer
};

export default Trend;
