import React from 'react';
import { LeagueConsumer } from './main.js';
import LeagueFrame from './frame.js';

const LeagueFrameContainer = (props) => {
	return <LeagueConsumer>
		{(context) => (
			createFrames(context.noOfFrames)
		)}
	</LeagueConsumer>
};

const createFrames = (count) => {
	let frames = [];

	for (let i = 0; i < count; i++) {
		frames.push(<LeagueFrame key={i} frameNo={i} />)
	}

	return frames;
};

export default LeagueFrameContainer;
