import React, { Component, createContext } from 'react';
import LeagueFrameContainer from './frame-container.js';
import LeagueFrameTotal from './frame-total.js';
import LeagueKeyboard from './keyboard.js';
import styled from 'styled-components';

const LeagueContext = createContext({});
const LeagueProvider = LeagueContext.Provider;
const LeagueConsumer = LeagueContext.Consumer;

const distanceLookup = {
	0: 5,
	1: 6,
	2: 7,
	3: 8,
	4: 9,
	5: 10
};

export class Main extends Component {
	constructor(props) {
		super(props);

		this.state = {
			noOfFrames: 20,
			currentTotal: 0,
			scoreMatrix: [],
			keyboard: {
				shouldShow: false,
				targetFrame: 0
			},
			showKeyboard: (target) => {
				this.setState({keyboard: {shouldShow: true, targetFrame: target}});
			},
			increaseTotal: () => {
				this.setState({currentTotal: this.state.currentTotal + 1});
			},
			inputChange: (frame, ev) => this.handleInputChange(frame, ev),
			scoreChange: (keyNo) => this.handleScoreChange(keyNo)
		}
	}

	componentDidMount() {
		this.createScoreMatrix(this.state.noOfFrames);
	}

	createScoreMatrix(noOfFrames) {
		let matrix = [];

		for (let i = 0; i < noOfFrames; i++) {
			matrix.push({distance: 0, score: 0, total: 0})
		}

		matrix[0].distance = 10;
		this.setState({scoreMatrix: matrix});
	}

	calculateTotal() {
		const { scoreMatrix } = this.state;
		let totalScore = 0;

		scoreMatrix.forEach((score) => {
			totalScore += (score.score * score.distance)
		})

		return totalScore;
	}

	handleInputChange(frame, ev) {
		const scoreMatrix = this.state.scoreMatrix;

		scoreMatrix[frame].score = ev.currentTarget.value;
		this.setState({scoreMatrix: scoreMatrix});
	}

	handleScoreChange(keyNo) {
		const { targetFrame } = this.state.keyboard;
		const { scoreMatrix } = this.state;
		let keyboard = {shouldShow: true, targetFrame: targetFrame + 1};

		scoreMatrix[targetFrame].score = keyNo;
		scoreMatrix[targetFrame].total = keyNo * scoreMatrix[targetFrame].distance;

		if ((targetFrame + 1) < this.state.noOfFrames) {
			scoreMatrix[targetFrame + 1].distance = distanceLookup[keyNo];
		}

		const currentTotal = this.calculateTotal();

		this.setState({
			scoreMatrix: scoreMatrix,
			keyboard: keyboard,
			currentTotal: currentTotal
		});
	}

	render () {
		return <LeagueContext.Provider value={this.state}>
			<LeagueMain>
				<LeagueFrameContainer />
				<LeagueFrameTotal />
			</LeagueMain>
			<LeagueKeyboardContainer>
				<LeagueKeyboard range={[0, 6]} />
			</LeagueKeyboardContainer>
		</LeagueContext.Provider>
	}
}

const LeagueMain = styled.div`
	width: 100%;
	height: 100%;
	padding: 40px;
	box-sizing: border-box;
	display: flex;
	flex-direction: row;
	justify-content: center;
	background: #f7f7f7;
`;

const LeagueKeyboardContainer = styled.div`
	padding: 50px;
	width: 100%;
	background: #f7f7f7;
	display: flex;
	flex-direction: row;
	justify-content: center;
	box-sizing: border-box;
`;

export {
	LeagueContext,
	LeagueProvider,
	LeagueConsumer
};

export default Main;
